* {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  ::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }

  ::-webkit-scrollbar-track {
    background: var(--back2p5-color);
  }

  ::-webkit-scrollbar-thumb {
    background: #3156fc;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #2341c8;
  }

  ::-webkit-scrollbar-thumb {
    border-radius: 10px;
  }

  &:focus {
    outline: none !important;
  }
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @supports (-webkit-appearance:none) and (not (overflow:-webkit-marquee)) {
    body {
      overflow-x: hidden !important;
    }
  }
}

.slide-routes {
  background: var(--back1-color);
}

.item.back-enter-done {
  will-change: width, transform;
}

.Menu {
  background-color: var(--back1-color);
  color: var(--text1-color);
  height: max-content;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  padding: 17px 0;
  overflow: hidden;
  -webkit-transition: background-color .3s, height .3s, padding .3s;
  -o-transition: background-color .3s, height .3s, padding .3s;
  transition: background-color .3s, height .3s, padding .3s;

  #bMenu h1,
  #bMenu span {
    display: none;
  }

  nav {
    position: relative;
    z-index: 2;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    text-align: center;
    max-width: calc(100vw - 30px);
    margin: auto;
    padding-top: 15px;
    padding-bottom: 15px;
    border-radius: 10px;
    background: var(--back1-color);
    color: var(--text1-color);

    border: 3px solid #2749de;
    box-shadow: 13px 13px 0px #2749de;
    -webkit-box-shadow: 13px 13px 0px #2749de;

    //transition: all .3s;

    -webkit-transition: background-color .3s;

    -o-transition: background-color .3s;

    transition: background-color .3s;

    a,
    >span {
      margin: 20px;
      text-decoration: none;
      font-size: 22px;
      color: var(--text2-color);
      letter-spacing: 1px;
      border-bottom: 3px solid #0000;
    }

    a {
      &:hover {
        border-bottom: 1px solid var(--text2-color);
      }

      &.active {
        border-bottom: 3px solid var(--text2-color);
      }
    }

    >span {
      margin: 0;
      margin-right: 20px;
      cursor: pointer;

      svg {
        width: 40px;
        height: 30px;

        -webkit-transform: translate(0, 6px) scale(1.2);

        -ms-transform: translate(0, 6px) scale(1.2);

        transform: translate(0, 6px) scale(1.2);

        path {
          fill: var(--text1-color);
        }
      }
    }
  }
}

@media (max-width: 1000px) {
  .Menu {
    padding-top: 0;
    background-color: var(--back1-color);
    overflow: hidden;
    transition: background-color .3s;

    #bMenu {
      display: block;
      height: 82px;
      color: #2749de;

      h1 {
        position: absolute;
        left: 20px;
        display: block;
        color: var(--text2-color);
      }
    }

    #bMenu .button {
      cursor: pointer;
      float: right;
      margin-top: 20px;
      margin-right: 20px;
      //display: inline-block;
      width: auto;

      -webkit-transition: all .7s ease;
      -o-transition: all .7s ease;
      transition: all .7s ease;

      .bar {
        display: block;
        height: 5px;
        width: 50px;
        background-color: #2749de;
        margin: 10px auto;
        -webkit-transition: all .7s ease;
        -o-transition: all .7s ease;
        transition: all .7s ease;
      }



      &.active {
        .top {
          -webkit-transform: translateY(15px) rotateZ(45deg);
          -ms-transform: translateY(15px) rotateZ(45deg);
          transform: translateY(15px) rotateZ(45deg);
        }

        .bottom {
          -webkit-transform: translateY(-15px) rotateZ(-45deg);
          -ms-transform: translateY(-15px) rotateZ(-45deg);
          transform: translateY(-15px) rotateZ(-45deg);
        }

        .middle {
          width: 0;
        }
      }
    }

    .relativeMenu {
      width: 100vw;
      height: calc(100vh - 82px);
      background-color: var(--back1-color);
      opacity: 0;
      will-change: opacity;
      position: absolute;
      overflow: auto;
      z-index: -1;
      overflow-x: hidden !important;
      //display: none;

      -webkit-transition: opacity .8s ease 0s, z-index 1.1s ease 0s, -webkit-transform 1.1s ease 0s;

      transition: opacity .8s ease 0s, z-index 1.1s ease 0s, background-color .3s, -webkit-transform 1.1s ease 0s;

      -o-transition: transform 1.1s ease 0s, opacity .8s ease 0s, z-index 1.1s ease 0s, background-color .3s;

      transition: transform 1.1s ease 0s, opacity .8s ease 0s, z-index 1.1s ease 0s, background-color .3s;

      transition: transform 1.1s ease 0s, opacity .8s ease 0s, z-index 1.1s ease 0s, background-color .3s, -webkit-transform 1.1s ease 0s;


      nav {
        position: absolute;
        z-index: 99;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 20px auto;
        background: var(--back1-color);
        width: 40vw;
        //display: none;

        /*min-width: -webkit-max-content;
        min-width: -moz-max-content;
        min-width: max-content;*/

        width: max-content;

        height: -webkit-max-content;
        height: -moz-max-content;
        height: max-content;

        max-width: calc(100vw - 110px);

        padding-left: 20px;
        padding-right: 20px;

        padding: 0 20px;

        -webkit-transform: translateX(100vw);
        -ms-transform: translateX(100vw);
        transform: translateX(100vw);

        will-change: transform;

        transition: transform 1s, background-color .3s;

        a,
        >span {
          display: block;

          width: -webkit-max-content;
          width: -moz-max-content;
          width: max-content;

          max-width: calc(100% - 20px);

          margin: auto;
          text-align: center;
          font-size: 35px;
          margin-top: 5vh;
          margin-bottom: 5vh;
          /*margin-left: 5vw;
          margin-right: 5vw;*/

          color: var(--text2-color);
        }

        a {
          &:hover {
            border-bottom: 3px solid var(--text2-color);
          }

          &.active {
            border-bottom: 6px solid var(--text2-color);
          }
        }

        a:first-child {
          margin-top: 30px;
        }

        span {
          margin-bottom: 30px;
        }
      }
    }

    &.active {
      //position: absolute;
      z-index: 99;
      width: 100vw;
      //height: 100%;
      padding: 0;
      padding-bottom: 20px;

      .relativeMenu {
        z-index: 98;
        opacity: 1;
        display: block;

        nav {
          display: block;
          -webkit-transform: translateX(0vw);
          -ms-transform: translateX(0vw);
          transform: translateX(0vw);
        }
      }

      #bMenu span {
        content: 'x';
      }
    }
  }
}

@media (max-height: 700px) {

  .Menu .relativeMenu nav a {
    margin-top: 4vh;
    margin-bottom: 4vh;
  }
}