@import '../../color.scss';


.TechnologyWatch {
  width: 100vw;

  margin: 100px auto;
  background: var(--back1-color);
  color: var(--text1-color);

  font-family: 'Poppins', sans-serif;
  text-align: center;

  transition: all .3s;
}

.TechnologyWatch h1 {
  position: relative;
  margin-top: 0;

  width: max-content;
  max-width: 100%;
  margin: auto;
  margin-bottom: auto;
  margin-bottom: 50px;
  font-size: 40px;
  color: #2749de;

  &::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    right: 0;
    margin: auto;
    background: #2749de;
    width: 100%;
    height: 6px;
    transform: translate(-11px, 0);
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 5px;
  }
}


.TechnologyWatch>p {
  position: relative;
  width: max-content;
  max-width: 100%;
  max-width: 50vw;
  margin: auto;
  margin-bottom: 50px;

  text-align: center;

  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 0 10px var(--back4-color);

  font-size: 20px;

  transition: all 0.3s;
}

.TechnologyWatch section {
  //display: flex;
  max-width: 90%;
  margin: auto;
  margin-top: 50px;

  border-radius: 20px;

  box-shadow: 0 0 20px var(--back4-color);
}

.TechnologyWatch section>div {
  display: inline-block;
  width: 400px;
  height: 550px;

  margin: auto;

  overflow: hidden;

  text-align: center;

  img {
    background-color: var(--back2p5-color);

    border-radius: 20%;

    margin-top: 40px;
    max-width: calc(100% - 30px);
    height: 250px;
  }

  p {
    width: 300px;
    height: 150px;

    margin: 40px auto;

    font-size: 17px;
  }
}

@media (max-width:1100px) {
  .TechnologyWatch>p {
    width: calc(100vw - 130px);
    max-width: calc(100vw - 130px);
  }
}

@media (max-width:700px) {

  .TechnologyWatch>p {
    width: calc(100vw - 80px);
    max-width: calc(100vw - 80px);

    padding: 20px;
  }


  .TechnologyWatch section>div {
    max-width: 100%;

    img {
      height: auto;
    }

    p {
      max-width: calc(100% - 50px);
    }
  }
}