@import '../../color.scss';

.Contact {
  min-height: calc(100vh - 110px);
  max-width: 100vw;
  //padding-bottom: 30px;

  font-family: 'Poppins', sans-serif;

  .Message {
    position: fixed;
    z-index: 100;
    top: 20px;
    left: 0;
    right: 0;
    margin: auto;
    background: #000;
    color: #fff;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    border-radius: 20px;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 1px;
    padding-bottom: 1px;
    opacity: 0;
    -webkit-transform: translate(0, -100px);
    -ms-transform: translate(0, -100px);
    transform: translate(0, -100px);
    -webkit-transition: all 0.7s;
    -o-transition: all 0.7s;
    transition: all 0.7s;

    &.active,
    .timer {
      -webkit-transform: translate(0, 0px);
      transform: translate(0, 0px);
      opacity: 1;
    }

    &.timer {
      cursor: pointer;

      -webkit-animation: 10s aparition;
      animation: 10s aparition;

      &::after {
        content: 'X';
        position: absolute;
        top: 5px;
        right: 15px;
        width: 5px;
        height: 5px;

        color: #111;
      }
    }
  }

  .flex {

    display: -webkit-box;

    display: -ms-flexbox;

    display: flex;

    .info {

      span {
        margin-top: 30px !important;
        margin-bottom: 15px !important;
      }

      .links {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
      }

      .email,
      .tel {
        padding-left: 15px;
        padding-right: 15px;
      }

      a {
        display: block;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        text-decoration: none;
        margin: auto !important;
        padding: 5px;
        font-size: 20px !important;
        border: 3px solid $color;
        border-radius: 50%;
        overflow: hidden;
        -webkit-transition: all .3s;
        -o-transition: all .3s;
        transition: all .3s;

        &:hover {
          background: $color;
          color: var(--back1-color) !important;

          svg {
            background: $color;

            path {
              fill: var(--back1-color)
            }
          }
        }


        svg {
          background: var(--back1-color);
          -webkit-transition: all .3s;
          -o-transition: all .3s;
          transition: all .3s;


          path {
            -webkit-transition: all .3s;
            -o-transition: all .3s;
            transition: all .3s;
            fill: $color;
          }

        }

        &#insta {
          path {
            stroke: $color;
            fill: #0000 !important;
          }

          &:hover {
            path {
              stroke: #fff;
            }
          }
        }
      }
    }
  }

  .info,
  form {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: center;
    min-width: 40vw;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;

    max-width: calc(100vw - 100px);
    min-height: 35vh;

    margin: 40px;
    margin-bottom: 45px;
    border-radius: 20px;
    padding: 30px;
    //border: 4px solid #2749de;
    //box-shadow: 10px 10px 0px #2749de;
    -webkit-box-shadow: 0px 0px 30px var(--back4-color);
    box-shadow: 0px 0px 30px var(--back4-color);

    color: var(--text1-color);

    h1 {
      position: relative;
      margin-top: 0;
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content;
      margin: auto;
      margin-bottom: 35px;
      //text-decoration: underline;
      font-size: 40px;
      color: $color;

      &::after {
        content: '';
        position: absolute;
        bottom: -5px;
        left: 0;
        right: 0;
        margin: auto;
        background: $color;
        width: 100%;
        height: 8px;
        -webkit-transform: translate(-21px, 0);
        -ms-transform: translate(-21px, 0);
        transform: translate(-21px, 0);
        padding-left: 20px;
        padding-right: 20px;
        border-radius: 5px;
      }
    }

    .top,
    .bottom {
      min-width: 300px;
      margin: 15px;
      margin-top: 0;

      span {
        display: block;
        font-size: 20px;
        margin: 3px;
      }

      .block {
        display: block;
        width: calc(100% - 15px);
        text-align: left;
        padding-left: 15px;
      }
    }

    .top {

      a,
      input {
        min-width: 200px;
        height: 30px;
        margin: 5px;
        border-radius: 5px;
        border: 2px solid #2749de;
        //box-shadow: 2px 2px 0px #2749de;
        color: var(--text1-color);
        background: var(--back2p5-color);
        text-align: center;
        margin-bottom: 20px;
        font-size: 18px;
        -webkit-transform: translate(0px, 0px);
        -ms-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
        -webkit-transition: all 0.2s;
        -o-transition: all 0.2s;
        transition: all 0.2s;

        &:focus-visible,
        &:hover {
          border: 2px solid #2749de;
          -webkit-transform: scale(0.95);
          -ms-transform: scale(0.95);
          transform: scale(0.95);
        }
      }
    }

    .bottom {
      width: 100%;

      textarea {
        max-width: calc(100% - 30px);
        border-radius: 5px;

        color: var(--text1-color);
        background: var(--back2p5-color);

        border: 2px solid #2749de;
        font-size: 16px;
        //box-shadow: 3px 3px 0px #2749de;
        -webkit-transform: translate(0px, 0px);
        -ms-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
        -webkit-transition: all 0.2s;
        -o-transition: all 0.2s;
        transition: all 0.2s;

        &:focus-visible,
        &:hover {
          border: 2px solid #2749de;
          //box-shadow: 0px 0px 0px #2749de;
          -webkit-transform: scale(0.97);
          -ms-transform: scale(0.97);
          transform: scale(0.97);
        }
      }

      input {
        display: block;
        width: 300px;
        height: 50px;
        border-radius: 13px;
        cursor: pointer;
        margin: auto;
        margin-top: 25px;
        color: var(--text1-color);
        background: var(--back2p5-color);
        font-family: 'Poppins', sans-serif;
        font-size: 17px;
        border: 3px solid #2749de;
        -webkit-box-shadow: 10px 10px 0px #2749de;
        box-shadow: 10px 10px 0px #2749de;
        -webkit-transform: translate(0px, 0px);
        -ms-transform: translate(0px, 0px);
        transform: translate(0px, 0px);
        -webkit-transition: all 0.2s;
        -o-transition: all 0.2s;
        transition: all 0.2s;

        &:focus-visible,
        &:hover {
          border: 3px solid #2749de;
          -webkit-box-shadow: 0px 0px 0px #2749de;
          box-shadow: 0px 0px 0px #2749de;
          -webkit-transform: translate(8px, 8px);
          -ms-transform: translate(8px, 8px);
          transform: translate(8px, 8px);
        }
      }
    }
  }
}


@media (min-width:1400px) {
  .Contact {
    .flex {
      .info {
        .links {
          width: -webkit-max-content;
          width: -moz-max-content;
          width: max-content;
          margin: auto;

          a {
            margin: 10px !important;
            margin-left: 30px !important;
            margin-right: 30px !important;
          }
        }
      }
    }
  }
}

@media (max-width:1400px) {
  .Contact {

    .flex {
      display: block;
      text-align: center;
      margin: auto;

      .info {
        margin: auto;
        margin-top: 30px;
        margin-bottom: 48px;
      }

      form {
        margin: auto;
        margin-bottom: 48px;
      }
    }
  }
}



@media (max-width:600px) {
  .Contact {

    .flex {

      .info,
      form {
        h1 {
          font-size: 30px;
        }
      }

    }
  }
}

@-webkit-keyframes aparition {
  0% {
    -webkit-transform: translate(0, -100px);
    transform: translate(0, -100px);
    opacity: 0;
  }

  5% {
    -webkit-transform: translate(0, 0px);
    transform: translate(0, 0px);
    opacity: 1;

  }

  95% {
    -webkit-transform: translate(0, 0px);
    transform: translate(0, 0px);
    opacity: 1;
  }

  100% {
    -webkit-transform: translate(0, -100px);
    transform: translate(0, -100px);
    opacity: 0;
  }
}

@keyframes aparition {
  0% {
    -webkit-transform: translate(0, -100px);
    transform: translate(0, -100px);
    opacity: 0;
  }

  5% {
    -webkit-transform: translate(0, 0px);
    transform: translate(0, 0px);
    opacity: 1;

  }

  95% {
    -webkit-transform: translate(0, 0px);
    transform: translate(0, 0px);
    opacity: 1;
  }

  100% {
    -webkit-transform: translate(0, -100px);
    transform: translate(0, -100px);
    opacity: 0;
  }
}

@media (max-width:500px) {
  .Contact {
    .flex {

      .info,
      form {
        h1 {
          font-size: 26px;

          &::after {
            padding-left: 10px;
            padding-right: 10px;
            -webkit-transform: translate(-11px, 0);
            -ms-transform: translate(-11px, 0);
            transform: translate(-11px, 0);

          }
        }

        .top,
        .bottom {
          min-width: auto;
          margin: auto;

          textarea {
            max-width: initial;
            width: 100%;
          }


        }

        .bottom input {
          width: 100%;
        }
      }

    }
  }
}

@media (max-width:500px) {

  .Contact .flex .info h1,
  .Contact .flex form h1 {
    max-width: 100%;
  }
}