@import '../../color.scss';


.AboutMe {
  min-height: calc(100vh - 232px);
  max-width: 100vw;
  padding-bottom: 30px;

  font-family: Akaya Kanadaka;
  text-align: center;

  .moi {
    position: relative;
    width: 100%;
    height: calc(100vh - 189px);
    color: $color;

    h1 {
      position: absolute;
      inset: 0;
      margin: auto;
      width: max-content;
      height: max-content;
      font-size: 50px;
    }
  }

  .card {
    aspect-ratio: 16 / 9;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: calc(50% - 60px);
    height: auto;
    margin: auto;
    margin-top: 9vh;
    margin-bottom: 2vh;
    overflow: hidden;
    background: gold;
    //background-color: #2749de31;
    background-color: var(--back2p5-color);
    background-image:
      -o-radial-gradient($color 2%, transparent 15%),
      -o-radial-gradient($color 2%, transparent 15%);
    background-image:
      radial-gradient($color 2%, transparent 15%),
      radial-gradient($color 2%, transparent 15%);
    //background-size: 20px 20px;
    background-size: 2vh 2vh;
    //background-position: 0 0, 10px 10px;
    background-position: 0 0, 1vh 1vh;
    border-radius: 30px;
    padding: 20px;
    -webkit-box-shadow: 0 0 10px var(--back2-color);
    box-shadow: 0 0 10px var(--back2-color);
    //cursor: pointer;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;

    &.fullscreen {
      position: relative;
      z-index: 3;
      -webkit-transform: translate3d(0vw, 20px, 0px) scale(1.3, 1.3) !important;
      transform: translate3d(0vw, 20px, 0px) scale(1.3, 1.3) !important;
      //background: #ffff;
      -webkit-transform-origin: 50% 50% 0px;
      -ms-transform-origin: 50% 50% 0px;
      transform-origin: 50% 50% 0px;
    }

    .image {
      position: relative;
      width: 40%;
      height: 100%;
      /*border-top: 2px solid #5b74e269;
      border-left: 2px solid #5b74e269;
      border-bottom: 2px solid #5b74e269;
      border-radius: 50px 0 0 50px;*/

      img {
        position: absolute;
        top: 30px;
        left: 0;
        right: 0;
        bottom: 0;
        //width: calc(100% - 50px);
        width: calc(100% - 4vh);
        margin: auto;
        //border-radius: 50px 5px;
        border-radius: 6vw 1vh;
        //background-color: #98a1d9;
        background-color: #586dc8;
        //background-color: #fff;
        //border: 3px solid $color;
      }
    }

    .content {
      width: 60%;
      height: 100%;
      /*border-top: 2px solid #5b74e269;
      border-right: 2px solid #5b74e269;
      border-bottom: 2px solid #5b74e269;
      border-radius: 0 50px 50px 0;*/

      .first {
        margin-top: calc(9vh - 10px);
      }


      //color: #fff;

      .flex {
        display: -webkit-box !important;
        display: -ms-flexbox !important;
        display: flex !important;

        .element {
          position: relative;
          background: var(--back3-color);
          color: var(--text1-color);
          margin-right: 4vw;
          border-bottom: 0.4vh solid $color;

          -webkit-transition: all .3s;

          -o-transition: all .3s;

          transition: all .3s;


          /*h1 {
            text-align: center;
          }*/


          &:hover .old {
            -webkit-transform: translate(-50%, 20px) scaleY(1);
            -ms-transform: translate(-50%, 20px) scaleY(1);
            transform: translate(-50%, 20px) scaleY(1);
            opacity: 1;
          }

          .old {
            position: absolute;
            z-index: 99;
            left: 50%;
            -webkit-transform: translate(-50%, -40px) scaleY(0);
            -ms-transform: translate(-50%, -40px) scaleY(0);
            transform: translate(-50%, -40px) scaleY(0);
            opacity: 0;
            width: -webkit-max-content;
            width: -moz-max-content;
            width: max-content;
            right: 0px;

            background: $color;
            color: #fff;
            padding: 15px;
            border-radius: 10px;

            font-size: 18px;
            text-align: center;

            -webkit-transition: all .3s;

            -o-transition: all .3s;

            transition: all .3s;

            &::before {
              content: "";
              position: absolute;
              z-index: -1;
              top: -10px;
              /* positionne le coin en haut */
              left: 50%;
              /* centre le coin horizontalement */
              -webkit-transform: translate(-50%, 2px) rotate(45deg);
              -ms-transform: translate(-50%, 2px) rotate(45deg);
              transform: translate(-50%, 2px) rotate(45deg);
              /* recentre le coin horizontalement */
              width: 30px;
              height: 30px;
              background-color: $color;
              border-radius: 4px
            }
          }
        }
      }

      .block {
        background: var(--back3-color);
        color: var(--text1-color);
        padding-bottom: 4px;
        border-bottom: 0.4vh solid $color;

        -webkit-transition: all .3s;

        -o-transition: all .3s;

        transition: all .3s;

      }

      .block,
      .flex {
        display: block;
        text-align: left;
        margin-left: 2vw;
        margin-bottom: 2vw;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        /*
        border: 3px solid $color;
        border-radius: 10px;
        padding: 10px;*/


        span {
          text-align: left;
          font-size: calc(1vw - 3px);
        }

        h1 {
          margin: 0;
          font-size: calc(2vw - 8px);
          text-align: center;
        }
      }

      .map {
        height: -webkit-max-content;
        height: -moz-max-content;
        height: max-content;

        a {
          text-decoration: none;
          color: var(--text1-color);

          -webkit-transition: all .3s;

          -o-transition: all .3s;

          transition: all .3s;

        }

        img {
          position: absolute;
          z-index: 99;
          left: 50%;
          -webkit-transform: translate(-50%, -150px) scaleY(0);
          -ms-transform: translate(-50%, -150px) scaleY(0);
          transform: translate(-50%, -150px) scaleY(0);
          opacity: 0;
          height: 250px;
          max-width: 50vw;
          right: 0px;
          border-radius: 20px;

          -webkit-box-shadow: 0 0 10px var(--back2-color);

          box-shadow: 0 0 10px var(--back2-color);

          -webkit-transition: all .3s;

          -o-transition: all .3s;

          transition: all .3s;
        }

        &:hover img {
          -webkit-transform: translate(-50%, 20px) scaleY(1);
          -ms-transform: translate(-50%, 20px) scaleY(1);
          transform: translate(-50%, 20px) scaleY(1);
          opacity: 1;
        }
      }


    }
  }

  .fleche {
    svg {
      width: 150px;
      height: 150px;

      -webkit-animation: apparition 4s, vas-vient_svg 2.8s infinite;

      animation: apparition 4s, vas-vient_svg 2.8s infinite;

      opacity: 1;


      -webkit-transform: rotate(90deg) translate(-10px, 0);


      -ms-transform: rotate(90deg) translate(-10px, 0);


      transform: rotate(90deg) translate(-10px, 0);

      * {
        stroke: var(--text1-color);
        stroke: $color;

        -webkit-transition: all .3s;

        -o-transition: all .3s;

        transition: all .3s;
      }

      line {
        -webkit-transform: translate(-3px, 0);
        -ms-transform: translate(-3px, 0);
        transform: translate(-3px, 0);
        border-radius: 50%;
      }

      polyline {
        -webkit-transform: translate(-3px, 0);
        -ms-transform: translate(-3px, 0);
        transform: translate(-3px, 0);

        -webkit-animation: vas-vient 2.8s infinite;

        animation: vas-vient 2.8s infinite;
      }
    }
  }

  .skills {
    margin-top: 70px;
    font-family: 'Poppins', sans-serif;
    color: var(--text1-color);

    -webkit-transition: all .3s;

    -o-transition: all .3s;

    transition: all .3s;

    h1 {
      position: relative;
      margin-top: 0;
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content;
      max-width: calc(100% - 50px);

      margin: auto;
      margin-bottom: 35px;
      //text-decoration: underline;
      font-size: 50px;
      color: $color;

      &::after {
        content: '';
        position: absolute;
        bottom: -5px;
        left: 0;
        right: 0;
        margin: auto;
        background: $color;
        width: 100%;
        height: 8px;
        -webkit-transform: translate(-21px, 0);
        -ms-transform: translate(-21px, 0);
        transform: translate(-21px, 0);
        padding-left: 20px;
        padding-right: 20px;
        border-radius: 5px;
      }
    }


    .divSkills {
      //display: flex;

      img {
        //width: 100px;
        height: 100px;

        -webkit-transition: height .3s ease 0s;

        -o-transition: height .3s ease 0s;

        transition: height .3s ease 0s;
      }

      /*.dev {
        //display: flex;
      }*/

      .dev,
      .network,
      .language {
        position: relative;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        max-width: calc(100vw - 120px);
        margin: auto;
        margin-bottom: 50px;
        padding: 40px;
        border-radius: 20px;
        /*border: 5px solid #2749de;
        -webkit-box-shadow: 10px 10px 0px #2749de;
        box-shadow: 10px 10px 0px #2749de;*/

        background: -o-linear-gradient(330deg, #2749de04 70%, #2749de 100%);

        background: linear-gradient(120deg, #2749de04 70%, #2749de 100%);

        //color: #fff;

        -webkit-box-shadow: 0 0 20px var(--back2-color);

        box-shadow: 0 0 20px var(--back2-color);

        -webkit-transition: all .3s;

        -o-transition: all .3s;

        transition: all .3s;


        .elfull {

          /*-webkit-tap-highlight-color: transparent;
          -webkit-touch-callout: none;
          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;*/

          will-change: transform, box-shadow, background-color;

          &:focus {
            outline: none !important;
          }

          //&.normal {}

          &.fullscreen {
            position: relative;
            z-index: 3;
            -webkit-transform: translate3d(0vw, -35px, 0px) scale(2, 2);
            transform: translate3d(0vw, -35px, 0px) scale(2, 2);
            background: var(--back1-color);
            -webkit-transform-origin: 50% 50% 0px;
            -ms-transform-origin: 50% 50% 0px;
            transform-origin: 50% 50% 0px;

            -webkit-box-shadow: 0 0 5px var(--back4-color);
            box-shadow: 0 0 5px var(--back4-color);

            img {
              will-change: height;
              height: 60px;
            }

            .folder {
              width: 70px;
              min-height: 60px;
              margin: auto;
              margin-bottom: -5px;
              margin-top: 0px;

              will-change: width, min-height;



              &.deux {
                width: 200px;
                margin-top: 0;

                img {

                  height: 60px;
                }
              }

              &.trois {
                width: 300px;
                margin-top: 0;

                img {
                  height: 100px;
                }
              }

              &.trois {
                width: 300px;
                margin-top: 0;

                img {
                  height: 60px;
                }
              }

              #symfony {
                position: relative;
                top: 10px;
                width: 40px;
                margin-left: 10px;
                margin-right: 10px;
              }

              .scale {
                -webkit-transform: scale(1.2);
                -ms-transform: scale(1.2);
                transform: scale(1.2);
              }

              img {
                //width: 50px;
                //margin: 5px;
                height: 30px;
                display: inline-block;
              }
            }

            h3 {
              margin-top: 5px;
              font-size: 15px;

              will-change: font-size;
            }

            p {
              margin-top: 0.5vh;
              height: auto;
              font-size: 13px;
              -webkit-transform: translate(0, 0px);
              -ms-transform: translate(0, 0px);
              transform: translate(0, 0px);

              will-change: font-size, transform;

            }
          }
        }

        &>div {
          display: inline-block;

          min-width: 200px;
          min-height: 170px;
          max-height: 170px;

          margin: 15px;
          padding: 5px;
          -webkit-box-shadow: 0 0 20px var(--back4-color);
          box-shadow: 0 0 20px var(--back4-color);
          border-radius: 10px;
          cursor: pointer;
          -webkit-transform: scale(1);
          -ms-transform: scale(1);
          transform: scale(1);
          overflow: hidden;

          -webkit-transition: all .3s ease 0s;
          -o-transition: all .3s ease 0s;
          transition: all .3s ease 0s;

          will-change: transform;

          &:hover {
            -webkit-transform: scale(1.2);
            -ms-transform: scale(1.2);
            transform: scale(1.2);
          }

          h3 {
            margin: 0;
            margin-top: 12px;
            font-size: 19px;

            -webkit-transition: font-size .3s ease 0s;

            -o-transition: font-size .3s ease 0s;

            transition: font-size .3s ease 0s;

            &::-moz-selection {
              background: #0000;
            }

            &::selection {
              background: #0000;
            }
          }

          p {
            //display: contents;
            //width: 100px;
            margin-top: 0;
            margin-left: auto;
            margin-right: auto;
            //height: 0;
            font-size: 14px;
            -webkit-transform: translate(0, 40px);
            -ms-transform: translate(0, 40px);
            transform: translate(0, 40px);

            -webkit-transition: font-size .3s ease 0s, -webkit-transform .3s ease 0s;

            transition: font-size .3s ease 0s, -webkit-transform .3s ease 0s;

            -o-transition: transform .3s ease 0s, font-size .3s ease 0s;

            transition: transform .3s ease 0s, font-size .3s ease 0s;

            transition: transform .3s ease 0s, font-size .3s ease 0s, -webkit-transform .3s ease 0s;


            &::-moz-selection {
              background: #0000;
            }


            &::selection {
              background: #0000;
            }
          }
        }
      }

      .network {
        background: -o-linear-gradient(20deg, #2749de 0%, #2749de04 30%);
        background: linear-gradient(70deg, #2749de 0%, #2749de04 30%);
      }

      /*.language {
        //background: linear-gradient(250deg, #2749de 0%, #2749de04 30%);
      }*/

      .folder {
        width: 100px;
        min-height: 100px;
        margin: auto;
        margin-bottom: -5px;
        margin-top: 0px;

        -webkit-transition: all .3s ease 0s;

        -o-transition: all .3s ease 0s;

        transition: all .3s ease 0s;

        &.deux {
          width: 200px;
          margin-top: 0;

          img {
            height: 100px;
          }
        }

        &.trois {
          width: 300px;
          margin-top: 0;

          img {
            height: 100px;
          }
        }

        #symfony {
          position: relative;
          top: 10px;
          width: 80px;
          margin-left: 10px;
          margin-right: 10px;
        }

        .scale {
          -webkit-transform: scale(1.2);
          -ms-transform: scale(1.2);
          transform: scale(1.2);
          //margin-right: -10px;
        }

        img {
          //width: 50px;
          height: 50px;
          display: inline-block;

          -webkit-transition: height .3s ease 0s, width .3s ease 0s;

          -o-transition: height .3s ease 0s, width .3s ease 0s;

          transition: height .3s ease 0s, width .3s ease 0s;
        }
      }
    }



  }

}

@-webkit-keyframes vas-vient {
  10% {
    -webkit-transform: translate(-3px, 0);
    transform: translate(-3px, 0);
  }

  50% {
    -webkit-transform: translate(2px, 0);
    transform: translate(2px, 0);
  }

  90% {
    -webkit-transform: translate(-3px, 0);
    transform: translate(-3px, 0);
  }
}

@keyframes vas-vient {
  10% {
    -webkit-transform: translate(-3px, 0);
    transform: translate(-3px, 0);
  }

  50% {
    -webkit-transform: translate(2px, 0);
    transform: translate(2px, 0);
  }

  90% {
    -webkit-transform: translate(-3px, 0);
    transform: translate(-3px, 0);
  }
}

@-webkit-keyframes apparition {

  0%,
  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes apparition {

  0%,
  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes vas-vient_svg {
  0% {
    -webkit-transform: rotate(90deg) translate(-25px, 0);
    transform: rotate(90deg) translate(-25px, 0);
  }

  50% {
    -webkit-transform: rotate(90deg)translate(15px, 0);
    transform: rotate(90deg)translate(15px, 0);
  }

  100% {
    -webkit-transform: rotate(90deg) translate(-25px, 0);
    transform: rotate(90deg) translate(-25px, 0);
  }
}

@keyframes vas-vient_svg {
  0% {
    -webkit-transform: rotate(90deg) translate(-25px, 0);
    transform: rotate(90deg) translate(-25px, 0);
  }

  50% {
    -webkit-transform: rotate(90deg)translate(15px, 0);
    transform: rotate(90deg)translate(15px, 0);
  }

  100% {
    -webkit-transform: rotate(90deg) translate(-25px, 0);
    transform: rotate(90deg) translate(-25px, 0);
  }
}

@media (max-width: 1300px) {
  .AboutMe {
    min-height: calc(100vh - 152px);

    .card {
      width: calc(70% - 60px);
      margin-top: 70px;

      .content {

        .first {
          margin-top: calc(4vw + 3px);
        }

        .flex {

          .element {
            margin-right: 5.2vw;
          }
        }

        .block,
        .flex {
          margin-bottom: 3vw;

          span {
            text-align: left;
            font-size: 1.2vw;
          }

          h1 {
            margin: 0;
            font-size: 2.4vw;
            text-align: center;
          }
        }
      }
    }
  }
}

@media (max-width: 900px) {
  .AboutMe {
    min-height: calc(100vh - 152px);

    .card {
      width: calc(100% - 80px);
      margin-top: 20px;

      /*height: calc(100vh - 100px);
      width: auto;*/

      &.fullscreen {
        position: relative;
        z-index: 3;
        -webkit-transform: translate3d(0vw, 35vh, 0px) scale(1.3, 1.3) rotate(90deg) !important;
        transform: translate3d(0vw, 35vh, 0px) scale(1.3, 1.3) rotate(90deg) !important;
        //background: #ffff;
        -webkit-transform-origin: 50% 50% 0px;
        -ms-transform-origin: 50% 50% 0px;
        transform-origin: 50% 50% 0px;
        -webkit-transform: translate(0, 0) !important;
        -ms-transform: translate(0, 0) !important;
        transform: translate(0, 0) !important;
      }

      .image {


        img {
          top: 20px;
          border-radius: 10vw 1vw;
        }
      }

      .content {

        .first {
          margin-top: calc(6vw + 10px);
        }

        .block,
        .flex {
          margin-bottom: 3vw;

          span {
            text-align: left;
            font-size: 2vw;
          }

          h1 {
            margin: 0;
            font-size: 3vw;
            text-align: center;
          }
        }
      }
    }

    .fleche {
      display: none;
    }

    .skills {
      .divSkills {

        .dev>div:hover {
          -webkit-transform: none;
          -ms-transform: none;
          transform: none;
        }

        .elfull {



          .folder.deux {

            width: 100% !important;
            margin-right: 10px;
            //margin: 5px;

            /*img {
              //margin: auto;
            }*/

            img {
              height: 90px;
            }
          }

          &.fullscreen {

            width: 170px !important;

            .folder.deux {
              width: 100% !important;
              margin-top: 0;

              img {
                height: 100px;
              }
            }

            &.trois {
              width: 250px !important;

              .folder {
                width: 100% !important;
              }
            }

          }
        }
      }
    }

    .content .map img {
      height: 250px !important;
      //width: 800px;
    }
  }
}

@media (max-width: 600px) {
  .AboutMe {
    .card {
      .content {

        .block,
        .flex {
          margin-top: 2vw;
          margin-bottom: 1vw;
        }

        .element .old,
        .map .img,
        .map svg {
          display: none;
        }


      }
    }
  }

  .skills {
    h1 {
      font-size: 43px !important;
    }

    .divSkills {


      .elfull {


        &.fullscreen {
          -webkit-transform: translate3d(0vw, -35px, 0px) scale(1.9, 1.9) !important;
          transform: translate3d(0vw, -35px, 0px) scale(1.9, 1.9) !important;
        }

      }

    }

  }
}

@media (max-width:500px) {

  .AboutMe .card .image img {
    border-radius: 7vw 1vw;
  }

  .AboutMe .card .content .block,
  .AboutMe .card .content .flex {
    margin-top: 1vw;
  }

  .AboutMe .skills h1 {
    font-size: 35px !important;
  }
}