@import '../../color.scss';


.Journey {
    min-height: calc(100vh - 232px);
    max-width: 100vw;
    padding-bottom: 30px;

    font-family: 'Poppins', sans-serif;
    text-align: center;

    color: #fff;

    .formations {
        position: relative;
        top: 50px;
        height: auto;
        //overflow-y: hidden;
        margin-top: 70px;
        margin-bottom: 200px;

        padding-bottom: 50px;


        &::before {
            content: '';
            background: $color;
            width: 6px;
            height: calc(100% + 100px);
            position: absolute;
            z-index: 0;
            top: -50px;
            left: 50%;
            -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
            transform: translateX(-50%);
            border-radius: 3px;
            -webkit-box-shadow: 0 0 20px #000;
            box-shadow: 0 0 20px #000;
        }

        .uneFormation {
            position: relative;
            //overflow: hidden;
            min-height: calc(85vh - 130px);
            width: 87%;
            margin: auto;
            margin-bottom: 130px;
            /*margin-left: 50px;
            margin-right: 50px;*/
            //padding-bottom: 20px;

            //border: 2px solid #fff;
            //border: 1px solid $color;
            //box-shadow: 0 0 20px rgba(0, 0, 0, 0.228);
            //box-shadow: 13px 13px 0 #2749de;
            -webkit-box-shadow: 0 0 20px var(--back4-color);
            box-shadow: 0 0 20px var(--back4-color);

            background: var(--back1-color);


            border-radius: 30px;

            -webkit-transition: all .3s;

            -o-transition: all .3s;

            transition: all .3s;
            //display: flex;
            //transform: scale(0.9);

            //transition: all 3s;

            .year {
                display: block;
                margin: auto;
                width: calc(100% - 20px);
                height: -webkit-max-content;
                height: -moz-max-content;
                height: max-content;
                color: $color;
                //background: $color;
                //background: #fff;
                padding: 10px;
                padding-bottom: 17px;
                //border-radius: 10px;
                //border: 5px solid #fff;
                //box-shadow: 0 0 50px #ffffff2e;
                font-size: 50px;
            }

            .flex {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;

                width: 100%;

                min-height: 400px;


                //background: #fff;
                //transition: all 5s;

                &:hover .xp {
                    width: 30%;

                    img {
                        height: 18vh;
                        max-height: 85%;

                        img.MaryseBastieL {
                            height: 10vh !important;
                        }
                    }

                    img {
                        height: 17vh;
                    }
                }

                .xp {
                    position: absolute;
                    z-index: 1;
                    width: 50%;
                    height: calc(100% - 87px);
                    min-height: 400px;
                    //border-radius: 30px;

                    //min-width: 500px;

                    color: #fff;
                    text-decoration: none;

                    overflow: hidden;

                    content-visibility: auto !important;

                    /*-webkit-transition: all .3s;

                    -o-transition: all .3s;

                    transition: all .3s;*/

                    -webkit-transition: width 0.3s ease 0s, height 0.3s ease 0s, -webkit-transform 0.3s ease 0s;
                    transition: width 0.3s ease 0s, height 0.3s ease 0s, -webkit-transform 0.3s ease 0s;
                    -o-transition: transform 0.3s ease 0s, width 0.3s ease 0s, height 0.3s ease 0s;
                    transition: transform 0.3s ease 0s, width 0.3s ease 0s, height 0.3s ease 0s;
                    transition: transform 0.3s ease 0s, width 0.3s ease 0s, height 0.3s ease 0s, -webkit-transform 0.3s ease 0s;
                    overflow: hidden;

                    will-change: width;

                    &#stage>img {
                        -webkit-transform: scale(1);
                        -ms-transform: scale(1);
                        transform: scale(1);

                        -webkit-transition: all .3s;

                        -o-transition: all .3s;

                        transition: all .3s;

                        /*&:hover {
                            -webkit-transform: scale(1.1);
                            -ms-transform: scale(1.1);
                            transform: scale(1.1);
                        }*/

                    }

                    &:first-child {
                        left: 0px;
                        border-right: 3px solid var(--back1-color);
                        border-radius: 0 0 0 30px;

                        -webkit-transition: all .3s;

                        -o-transition: all .3s;

                        transition: all .3s;

                    }

                    &:last-child {
                        right: 0px;
                        border-left: 3px solid var(--back1-color);
                        border-radius: 0 0 30px 0;

                        -webkit-transition: all .3s;

                        -o-transition: all .3s;

                        transition: all .3s;

                    }

                    &:hover {
                        z-index: 10;
                        width: 70%;

                        img {
                            height: 20vh;
                        }
                    }

                    &.valadon {
                        background: -o-linear-gradient(315deg, #333333ca 0%, #33333385 100%),
                            url('../../../public/Journey/img/valadon.png'), #000;
                        background: linear-gradient(135deg, #333333ca 0%, #33333385 100%),
                            url('../../../public/Journey/img/valadon.png'), #000;
                        background-position: center;
                        background-size: cover;
                    }

                    &.laposte {
                        background: -o-linear-gradient(right, #333333ca 0%, #33333385 100%),
                            url('../../../public/Journey/img/laposte.jpg'), #000;
                        background: -webkit-gradient(linear, right top, left top, from(#333333ca), to(#33333385)),
                            url('../../../public/Journey/img/laposte.jpg'), #000;
                        background: linear-gradient(270deg, #333333ca 0%, #33333385 100%),
                            url('../../../public/Journey/img/laposte.jpg'), #000;
                        background-position: center;
                        background-size: cover;
                    }

                    &.MaryseBastieL {
                        background: -o-linear-gradient(right, #333333ca 0%, #33333385 100%),
                            url('../../../public/Journey/img/MaryseBastieL.png'), #000;
                        background: -webkit-gradient(linear, right top, left top, from(#333333ca), to(#33333385)),
                            url('../../../public/Journey/img/MaryseBastieL.png'), #000;
                        background: linear-gradient(270deg, #333333ca 0%, #33333385 100%),
                            url('../../../public/Journey/img/MaryseBastieL.png'), #000;
                        background-position: center;
                        background-size: cover;
                        //padding-bottom: 25px;
                    }

                    &.stages {
                        background: -o-linear-gradient(right, #33333383 0%, #33333385 100%),
                            url('../../../public/Journey/img/stages.png'), #000;
                        background: -webkit-gradient(linear, right top, left top, from(#33333383), to(#33333385)),
                            url('../../../public/Journey/img/stages.png'), #000;
                        background: linear-gradient(270deg, #33333383 0%, #33333385 100%),
                            url('../../../public/Journey/img/stages.png'), #000;
                        background-position: center;
                        background-size: cover;

                        /*a img {
                            height: 20vh;
                        }*/
                    }

                    .back {
                        position: absolute;
                        top: 20px;
                        left: 20px;

                        display: -webkit-box;

                        display: -ms-flexbox;

                        display: flex;
                        -webkit-box-orient: vertical;
                        -webkit-box-direction: normal;
                        -ms-flex-direction: column;
                        flex-direction: column;

                        width: -webkit-max-content;

                        width: -moz-max-content;

                        width: max-content;

                        text-decoration: none;
                        background: $color;
                        color: var(--back1-color);
                        padding: 0px 20px 10px 20px;

                        margin: 20px auto 0 auto;

                        color: #fff;

                        border-radius: 10px;
                        overflow: hidden;

                        cursor: pointer;

                        opacity: 0;

                        -webkit-transition: all 0.3s;

                        -o-transition: all 0.3s;

                        transition: all 0.3s;

                        &:hover {
                            -webkit-transform: scale(1.1);
                            -ms-transform: scale(1.1);
                            transform: scale(1.1);
                        }

                        img {
                            margin-top: 2px;
                            margin-bottom: 10px;
                            height: 25px;
                            -webkit-transform: translate(0, 7px) rotate(180deg);
                            -ms-transform: translate(0, 7px) rotate(180deg);
                            transform: translate(0, 7px) rotate(180deg);
                            -webkit-transition: all 0.3s;
                            -o-transition: all 0.3s;
                            transition: all 0.3s;
                        }
                    }

                    &.active .back {
                        opacity: 1;
                    }

                    .nom {
                        width: -webkit-max-content;
                        width: -moz-max-content;
                        width: max-content;
                        max-width: 80%;
                        margin: auto;
                        margin-top: 30px;
                        margin-bottom: 30px;
                        border-bottom: 3px solid $color;

                        transition: all .3s;
                    }

                    >img {
                        margin: 10px;
                        //height: 200px;
                        height: 20vh;
                        //max-width: 80%;
                        border-radius: 20px;

                        -webkit-transition: all .3s;

                        -o-transition: all .3s;

                        transition: all .3s;

                        &.valadon {
                            background: #fff;
                            padding-left: 10px;
                            padding-right: 10px;
                        }

                        &.MaryseBastieL {
                            background: #0003;
                            padding: 10px;
                            border-radius: 50px;
                        }
                    }


                    .info,
                    .localisation {
                        position: relative;
                        bottom: 10px;
                        left: 0;
                        right: 0;
                        width: -webkit-max-content;
                        width: -moz-max-content;
                        width: max-content;
                        max-width: calc(100vw - 50px);

                        margin: auto;
                        margin-bottom: 20px;
                        background: #00000068;
                        border-radius: 30px;
                        display: block;

                        padding: 5px;

                        -webkit-transform: translate(0, 150px);

                        -ms-transform: translate(0, 150px);

                        transform: translate(0, 150px);
                        opacity: 0;

                        -webkit-transition: all .3s;

                        -o-transition: all .3s;

                        transition: all .3s;
                    }

                    .localisation {
                        display: -webkit-box !important;
                        display: -ms-flexbox !important;
                        display: flex !important;
                        min-height: 200px;

                        max-width: calc(100vw - 60px);

                        -webkit-box-pack: center;

                        -ms-flex-pack: center;

                        justify-content: center;

                        -webkit-box-align: center;

                        -ms-flex-align: center;

                        align-items: center;

                        h2 {
                            margin-left: 30px;
                        }

                        span {
                            display: -webkit-box;
                            display: -ms-flexbox;
                            display: flex;
                            -webkit-box-orient: vertical;
                            -webkit-box-direction: normal;
                            -ms-flex-direction: column;
                            flex-direction: column;
                            padding: 20px;
                            padding-left: 30px;
                            padding-right: 30px;

                            >img {
                                height: 200px !important;
                                width: 200px;
                                margin-bottom: 0;
                            }

                            span {
                                text-align: center;
                                color: #fff;

                                font-size: 22px;
                            }

                            .carte {
                                position: relative;
                                padding: 10px;
                                padding-right: 55px;
                                background: rgb(248, 38, 38);
                                border-radius: 20px;

                                font-size: 18px;

                                cursor: pointer;

                                overflow: hidden;
                                -webkit-transition: all .3s;
                                -o-transition: all .3s;
                                transition: all .3s;

                                >img {
                                    position: absolute;
                                    top: 2px;
                                    right: 10px;
                                    bottom: 0;
                                    margin: auto;
                                    width: 40px;
                                    height: 40px !important;

                                    -webkit-transition: all .3s;

                                    -o-transition: all .3s;

                                    transition: all .3s;
                                }


                                &:hover {

                                    padding-right: 32.5px;
                                    padding-left: 32.5px;

                                    & img {
                                        -webkit-transform: translate(50px, 0);
                                        -ms-transform: translate(50px, 0);
                                        transform: translate(50px, 0);
                                    }
                                }
                            }
                        }

                    }

                    .info {
                        width: 80%;
                        min-height: 300px;

                        margin-bottom: calc(9vw + 15px);

                        p {
                            font-size: 18px;

                            span {
                                font-weight: bold;
                                font-size: 22px;
                            }
                        }
                    }

                    &.active {
                        min-height: -webkit-max-content;
                        min-height: -moz-max-content;
                        min-height: max-content;
                        overflow: auto;

                        cursor: auto;

                        >img {
                            height: 18vh;
                            margin-bottom: 30px;
                        }

                        .info,
                        .localisation {
                            display: block;

                            /*-webkit-backdrop-filter: blur(2px);
                        backdrop-filter: blur(2px);
                        -webkit-box-shadow: 0 0 15px #6262628c;
                        box-shadow: 0 0 15px #6262628c;*/

                            -webkit-transform: translate(0, 0px);

                            -ms-transform: translate(0, 0px);

                            transform: translate(0, 0px);
                            opacity: 1;
                        }
                    }
                }
            }


        }
    }

}

.fullscreen.Journey {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
    min-height: -webkit-max-content;
    min-height: -moz-max-content;
    min-height: max-content;
    height: 0;

    background: -o-linear-gradient(right, #333333ca 0%, #33333385 100%),
        url('../../../public/Journey/img/black.png');

    background: -webkit-gradient(linear, right top, left top, from(#333333ca), to(#33333385)),
        url('../../../public/Journey/img/black.png');

    background: linear-gradient(270deg, #333333ca 0%, #33333385 100%),
        url('../../../public/Journey/img/black.png');

    -webkit-transform: translate(0, -100vh);

    -ms-transform: translate(0, -100vh);

    transform: translate(0, -100vh);

    padding-bottom: 0px;

    -webkit-transition: all .3s;

    -o-transition: all .3s;

    transition: all .3s;
}

/*@media (min-width: 900px) {
    .Journey .formations .uneFormation .flex .xp {
        width: calc(50% - 3px);
    }
}*/

@media (max-width: 1400px) {
    .Journey .formations .uneFormation .flex {

        &:hover .xp {
            width: 20%;
        }

        .xp {
            //width: auto;
            will-change: height;


            &:hover {
                width: 80%;
            }

            .nom {
                width: auto;
                max-width: 70%;
            }

            >img {
                margin: 5px;
                height: 16vh !important;


                &.MaryseBastieL {
                    height: 14vh !important;
                }
            }

            &#stage>img {
                height: 14vh !important;
            }
        }
    }

}

@media (max-width: 900px) {
    .Journey .formations {
        margin-top: 20px;

        &::before {
            -webkit-box-shadow: 0 0 20px #0008;
            box-shadow: 0 0 20px #0008;
        }

        .uneFormation {
            width: 92%;
            min-height: calc(130vh - 130px);

            .year {
                padding-bottom: 10px;
            }

            .flex {
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -ms-flex-direction: column;
                flex-direction: column;
                height: 200%;

                min-height: 800px;

                &:hover .xp {
                    width: 100%;
                }

                .xp {
                    width: 100%;
                    height: calc(50% - 50px);

                    &:first-child {
                        left: 0;
                    }

                    &:last-child {
                        right: 0;
                    }

                    &:hover {
                        width: 100%;
                    }

                    &.active .nom {
                        max-width: 60%;

                        margin-top: 100px;
                    }

                    .back {
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;

                    }

                    .nom {
                        width: -webkit-max-content;
                        width: -moz-max-content;
                        width: max-content;

                        max-width: 90%;
                    }

                    img.MaryseBastieL {
                        height: 14vh !important;
                    }

                    &:first-child {
                        //top: 0;
                        border-right: initial;
                        border-bottom: 2px solid #fff;
                        border-radius: 0;

                    }

                    &:last-child {
                        bottom: 0;
                        border-left: initial;
                        border-top: 2px solid #fff;
                        border-radius: 0 0 30px 30px;
                    }

                    .localisation,
                    .info {
                        -webkit-transform: translate(0, 0px) !important;

                        -ms-transform: translate(0, 0px) !important;

                        transform: translate(0, 0px) !important;
                    }

                    .localisation {
                        -webkit-box-orient: vertical;
                        -webkit-box-direction: normal;
                        -ms-flex-direction: column;
                        flex-direction: column;


                        h2 {
                            margin-left: 0;
                        }

                        span .carte:hover {
                            padding: 10px;
                            padding-right: 55px;

                            img {
                                transform: none;
                            }
                        }
                    }
                }
            }
        }
    }
}