@import '../../color.scss';

.legalNotice {
    position: relative;
    z-index: 10;

    display: flex;
    flex-direction: column;

    width: 100vw;
    min-height: calc(100vh - 200px);

    //padding-bottom: 30px;

    text-align: center;
    color: var(--text1-color);
    background: var(--back1-color);


    margin: auto;

    font-family: 'Poppins', sans-serif;

    .back {
        position: absolute;
        top: 0;
        left: 20px;

        display: -webkit-box;

        display: -ms-flexbox;

        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;

        width: -webkit-max-content;

        width: -moz-max-content;

        width: max-content;

        text-decoration: none;
        background: $color;
        color: var(--back1-color);
        padding: 0px 20px 10px 20px;

        margin: 20px auto 0 auto;

        color: #fff;

        border-radius: 10px;
        overflow: hidden;

        cursor: pointer;

        -webkit-transition: all 0.3s;

        -o-transition: all 0.3s;

        transition: all 0.3s;

        &:hover {
            -webkit-transform: scale(1.1);
            -ms-transform: scale(1.1);
            transform: scale(1.1);
        }

        img,
        svg {
            margin-top: 2px;
            margin-bottom: 10px;
            height: 25px;
            -webkit-transform: translate(0, 7px) rotate(180deg);
            -ms-transform: translate(0, 7px) rotate(180deg);
            transform: translate(0, 7px) rotate(180deg);
            -webkit-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
        }
    }

    h1,
    h2 {
        position: relative;
        margin-top: 0;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        margin: auto;
        margin-bottom: 35px;
        //text-decoration: underline;
        font-size: 40px;
        color: $color;

        &::after {
            content: '';
            position: absolute;
            bottom: -5px;
            left: 0;
            right: 0;
            margin: auto;
            background: $color;
            width: 100%;
            height: 8px;
            -webkit-transform: translate(-21px, 0);
            -ms-transform: translate(-21px, 0);
            transform: translate(-21px, 0);
            padding-left: 20px;
            padding-right: 20px;
            border-radius: 5px;
        }
    }

    h1 {
        display: block;
        margin: 80px auto;
    }

    h2 {
        font-size: 30px;

        &::after {
            height: 4px;
        }
    }

    p {
        margin-bottom: 50px;
    }

    a {
        color: $color;
    }
}


@media (max-width: 900px) {
    .back {
        position: relative !important;
        left: 0 !important;
    }

    h1 {
        margin-top: 20px !important;
    }

    p {
        max-width: calc(100vw - 30px) !important;

        margin: auto;
    }
}