@import '../../color.scss';

.Projects {
    min-height: calc(100vh - 241px);
    max-width: 100vw;
    padding-bottom: 30px;

    font-family: 'Poppins', sans-serif;
    text-align: center;

    background: var(--back1-color);
    color: var(--text1-color);

    -webkit-transition: all .3s;

    -o-transition: all .3s;

    transition: all .3s;

    .filter,
    .synthese {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin: auto;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        max-width: calc(100vw - 100px);
        height: 50px;

        margin-top: 100px;

        //border: 3px solid $color;
        padding: 20px 80px;
        border-radius: 20px;

        //box-shadow: 13px 13px 0 #2749de;
        -webkit-box-shadow: 0 0 10px var(--back4-color);
        box-shadow: 0 0 10px var(--back4-color);


        -webkit-transition: all .3s;


        -o-transition: all .3s;


        transition: all .3s;


        h2,
        h3,
        input,
        select {
            margin: 10px;
            text-align: center;
            //color: $color;


        }

        h2,
        h3 {
            position: relative;
            top: calc(50% + 4px);

            height: -webkit-max-content;

            height: -moz-max-content;

            height: max-content;

            -webkit-transform: translate(0, -50%);

            -ms-transform: translate(0, -50%);

            transform: translate(0, -50%);
            margin: 0 auto;
        }

        input,
        select {
            height: 35px;
            border: 1px solid $color;
            background: var(--back2p5-color);
            color: var(--text1-color);
            border-radius: 5px;

            -webkit-transition: all .3s;

            -o-transition: all .3s;

            transition: all .3s;

        }

        select {
            padding: 0 50px;
        }
    }

    .synthese {
        display: flex;
        flex-direction: column;

        height: max-content;

        h2 {
            margin-top: 30px;
        }

        a {
            width: max-content;
            padding: 5px 20px;

            margin: auto;

            text-decoration: none;

            border: 1px solid #2749de;
            background-color: var(--back2p5-color);
            color: var(--text1-color);
            border-radius: 5px;
            transition: all 0.3s;

            &:hover {
                background-color: $color;
                color: #fff;
            }
        }
    }

    .liste {
        display: block;
        margin: auto;
        //max-width: max-content;
        width: calc(100vw - 500px);
        height: -webkit-max-content;
        height: -moz-max-content;
        height: max-content;

        margin-top: 100px;

        //border: 3px solid $color;
        padding: 20px 0px;
        border-radius: 20px;

        //box-shadow: 13px 13px 0 #2749de;

        .aProject {
            position: relative;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            width: calc(100% - 80px);
            height: 350px;

            padding: 0 50px;
            margin: 30px auto;
            border-radius: 30px;

            //cursor: pointer;

            -webkit-box-shadow: 0 0 10px var(--back2-color);

            box-shadow: 0 0 10px var(--back2-color);

            -webkit-transition: all .3s;

            -o-transition: all .3s;

            transition: all .3s;

            &:hover {
                -webkit-box-shadow: 0 0 20px var(--back2-color);
                box-shadow: 0 0 20px var(--back2-color);
            }

            div {
                position: relative;
                top: 50%;
                -webkit-transform: translate(0, -50%);
                -ms-transform: translate(0, -50%);
                transform: translate(0, -50%);
                height: -webkit-max-content;
                height: -moz-max-content;
                height: max-content;
                margin: 0;
                width: 30%;

            }

            .name h1 {
                border-bottom: 2px solid $color;
                width: -webkit-max-content;
                width: -moz-max-content;
                width: max-content;
                max-width: 100%;
                margin: auto;
                padding: 0 10px;
            }


            >img,
            >svg {
                height: 90%;
                margin: auto;

                border-radius: 30px;
            }

            .detail {
                font-size: 18px;
                width: 20%;

                padding-bottom: 40px;

                h4 {
                    margin: 15px 0;

                    &:first-child {
                        margin-top: 0;
                    }

                    &:last-child {
                        margin-bottom: 5px;
                    }
                }

                img {
                    margin: 5px;
                }

                .logo {
                    display: inline-block;
                    height: 50px;
                }
            }

            a {
                position: absolute;
                bottom: 20px;
                right: 30px;
                text-decoration: none;

                background: $color;
                color: #fff;
                padding: 10px;
                padding-right: 40px;

                border-radius: 10px;

                overflow: hidden;

                -webkit-transition: all .3s;

                -o-transition: all .3s;

                transition: all .3s;

                img {
                    position: absolute;
                    top: 2px;
                    right: 10px;
                    bottom: 0;
                    margin: auto;

                    height: 25px;
                    //width: 20px;

                    -webkit-transition: all .3s;

                    -o-transition: all .3s;

                    transition: all .3s;
                }

                &:hover {
                    padding-left: 25px;
                    padding-right: 25px;

                    img {
                        -webkit-transform: translate(40px, 0);
                        -ms-transform: translate(40px, 0);
                        transform: translate(40px, 0);
                    }
                }
            }


        }
    }
}


@media (max-width:1700px) {
    .Projects .liste {
        width: calc(100vw - 200px);

        .aProject {
            padding: 0 25px;
        }
    }
}

@media (max-width:1450px) {
    .Projects .liste {
        width: calc(100vw - 150px);

        .aProject {
            height: 370px;

            .name {
                width: 15%;
            }

            img,
            svg {
                height: auto;
                max-height: 90%;
                width: auto;
                max-width: 55%;
            }

            .detail h4 {
                margin: 10px 0;
            }

            a {
                bottom: 10px;
            }
        }
    }
}

@media (max-width:1300px) {
    .Projects {

        .filter {
            margin-top: 50px;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            height: -webkit-max-content;
            height: -moz-max-content;
            height: max-content;

            h2,
            h3 {
                position: none;
                top: 0;
                height: auto;
                -webkit-transform: none;
                -ms-transform: none;
                transform: none;
            }
        }

        .liste {
            margin-top: 50px;
            width: calc(100vw - 20px);

            .aProject {
                height: 380px;

                .name {
                    width: 20%;
                }

                img,
                svg {
                    max-height: 85%;
                    max-width: 50%;

                }

                .logo {
                    margin: 1px !important;

                }
            }
        }
    }
}


@media (max-width:900px) {
    .Projects .liste {
        width: calc(100vw - 20px);

        .aProject {
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            height: -webkit-max-content;
            height: -moz-max-content;
            height: max-content;

            .name,
            img,
            svg,
            .detail {
                display: block;
                width: 100%;
            }

            .name,
            .detail {
                position: relative;
                top: 0;
                -webkit-transform: none;
                -ms-transform: none;
                transform: none;
                margin: 20px 0;
            }

            img {
                //max-width: 90%;
                max-height: 30vh;
            }

            .detail {
                padding-bottom: 0;

                .logo {
                    width: 50px;
                    height: auto;
                }
            }

            a {
                position: relative;
                inset: 0;
                margin: auto;
                width: calc(100% - 150px);

                padding: 8px 25px;

                margin-bottom: 25px;


                img {
                    display: none;
                }
            }

        }
    }
}